<template>
  <div id="app">
    <div class="header">
    <span v-if="pd==1">新增合同<span style="color:red;">未提交</span></span>
    <span v-if="pd==2">新增合同<span style="color:red;">已提交</span></span>
    <span v-if="pd==3">新增合同<span style="color:red;">已拒绝</span></span>
    <div class="header-right">
      <el-button size="small" type="warning" @click="Refresh">刷新</el-button>
    </div>
    </div>
    <!-- 下方列表显示 -->
    <el-row :gutter="10">
   <el-col :xs="6" :sm="6" :md="4" :lg="4" :xl="4">
      <div class="box">
          <ul>
            <li :class="pd==1?'list':''" @click="change(1)">未提交</li>
            <li :class="pd==2?'list':''" @click="change(2)">已提交</li>
            <li :class="pd==3?'list':''" @click="change(3)">已拒绝</li>
          </ul>
      </div>
      </el-col>
       <el-col :xs="18" :sm="18" :md="20" :lg="20" :xl="20">
      <div class="box-cont">
          <notContract v-if="pd==1" ref="notContract"></notContract>
          <yesContract v-if="pd==2" ref="yesContract"></yesContract>
          <noContract v-if="pd==3" ref="noTendering"></noContract>
      </div>
      </el-col>
      </el-row>
  </div>
</template>
<script>
import notContract from './notContract'//未提交
import yesContract from './yesContract'//已提交
import noContract from './noContract'//已拒绝
export default {
  components:{
    notContract,
    yesContract,
    noContract
  },
  data(){
    return{
      pd:1
    }
  },
  methods:{
    change(number){//切换列表
      this.pd = number;
    },
    Refresh(){//刷新
      if(this.pd==1){
        this.$refs.notContract.axios();
      }
      if(this.pd==2){
        this.$refs.yesContract.axios();
      }
      if(this.pd==3){
        this.$refs.noContract.axios();
      }
    }
  }
}
</script>
<style scoped>
.header{
      display: flex;
      justify-content: space-between;
      padding: 10px 0;
    }
    .header span{
      vertical-align: middle;
      line-height: 32px;
    }
    .header .header-right{
      vertical-align: middle;
    }

    .box{
      text-align: center;
      border: 1px solid #eee;
    }
    .box ul .list{
      background: #3e3e3e !important;
      border-radius: 5px;
      color: white;
    }
    .box ul li{
      margin: 10px 10px;
      padding: 5px 0;
      cursor: pointer;
      transition: .5s all;
    }
    .box ul li:hover{
      background: #999;
      color: white;
      border-radius: 5px;
    }

    .box ul{
      list-style: none;
      margin: 0;
      padding: 0;
    }
</style>



